<template lang="pug">
.curso-main-container.pb-3
  BannerInterno
  .container.tarjeta.tarjeta--blanca.p-4.p-md-5.mb-5
    #t_1_1
    .titulo-segundo.color-acento-contenido(data-aos="fade-right")
      h2 1.1.	Concepto, estructura y partes de un recorrido turístico

    .row.justify-content-center.align-items-center.mb-4(data-aos="flip-up")
      p El recorrido turístico puede definirse como una experiencia planificada que busca generar conocimiento, disfrute y sensibilización en las personas visitantes. Según Gunn y Var (2020), un recorrido bien diseñado combina componentes logísticos, interpretativos y comunicativos, asegurando una experiencia coherente, educativa y significativa. En ese sentido, toda actividad interpretativa debe ir más allá de la simple transmisión de datos, pues su propósito es revelar significados profundos y generar vínculos emocionales entre el visitante y el lugar (Tilden, 2008). De este modo, un recorrido turístico no solo muestra atractivos, sino que narra una historia, un relato que conecta a las personas con la esencia del territorio y con las identidades culturales que lo habitan.
     
    .row.justify-content-center.align-items-center.mb-2(data-aos="fade-left")
      .col-md-1
      .col-md-8.mb-4.mb-md-0.my-2
        p Un recorrido turístico también puede entenderse como una experiencia educativa y vivencial, donde el guía turístico actúa como mediador del conocimiento, facilitando la comprensión del entorno mediante técnicas interpretativas y recursos sensoriales. En este rol, el guía se convierte en un estratega que planifica, anticipa circunstancias y conoce profundamente el territorio, asegurando la seguridad, la coherencia narrativa y la sostenibilidad de la experiencia (Posada-Swafford, 2023). Un recorrido turístico se estructura en tres fases operativas fundamentales: inicio, desarrollo y cierre, las cuales garantizan una experiencia organizada, segura y enriquecedora para las personas visitantes. Un recorrido turístico se compone de tres fases operativas, que se resumen en la siguiente tabla:
      .col-md-3
        figure
          img(src='@/assets/curso/temas/tema1/tema1/tema1-img1.svg', alt='El diseño y la organización de paquetes turísticos')
        
    br
    .row.justify-content-center(data-aos="fade-left")
      .col-lg-10
        figure
          img(src='@/assets/curso/temas/tema1/tema1/tema1-tabla1.svg', alt='Texto que describa la imagen')
   
    br
    .row.justify-content-center.align-items-center.mb-4(data-aos="flip-up")
      p Estas etapas proporcionan una estructura secuencial que favorece la coherencia y la calidad del recorrido. Es fundamental que el guía turístico resalte las características distintivas del destino, aquellas que reflejan su autenticidad cultural, su riqueza natural y las razones por las cuales fue elegido por los visitantes.
      .p Los recorridos turísticos en entornos étnicos y sostenibles deben integrar las cosmovisiones, saberes tradicionales y prácticas culturales de las comunidades locales, fortaleciendo el diálogo intercultural y la apropiación social del patrimonio. El diseño del recorrido no se limita a definir un trayecto o seleccionar atractivos; implica un proceso integral de planificación, ejecución y evaluación, que garantiza la calidad de la experiencia y la sostenibilidad del destino. 
      .p Ahora bien, es fundamental conocer de qué trata cada una de estas fases:

    .row.justify-content-center(data-aos="fade-right")
      .col-md-11.mb-5.mb-md-0
        LineaTiempoD
          .row(numero="Planificación:&nbsp;&nbsp" titulo="")
            .col-md-3
              figure
                img(src='@/assets/curso/temas/tema1/tema1/tema11-rec3-img1.svg', alt='Texto que describa la imagen')
                
            .col-md-9.mb-4.mb-md-0
              p.color-adicional-4.lh-1 Esta fase busca que cada elemento del recorrido turístico responda a una narrativa clara y a las capacidades logísticas, ambientales y étnicas del territorio. Incluye la definición de objetivos, la identificación de recursos turísticos, el diseño del itinerario y la selección de puntos de interés. 
              p.color-adicional-4.lh-1 Se determinan los puntos de encuentro, las paradas interpretativas y los espacios de descanso, asegurando la coherencia temática y la seguridad de las personas visitantes. Asimismo, se establecen los protocolos de atención, las normas de comportamiento y las estrategias de interpretación que orientarán el trabajo del guía turístico.

          .row(numero="Ejecución:&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp" titulo="")
            .col-md-3
              figure
                img(src='@/assets/curso/temas/tema1/tema1/tema11-rec3-img2.svg', alt='Texto que describa la imagen')
                
            .col-md-9.mb-4.mb-md-0
              p.color-adicional-4.lh-1 Corresponde al momento en que el recorrido se realiza con las personas visitantes, poniendo en práctica todas las acciones planificadas bajo la estructura diseñada. 
              p.color-adicional-4.lh-1 Durante esta fase, el guía gestiona la interacción del grupo, el control de tiempos, las pausas de descanso y las dinámicas de integración. La ejecución debe mantener la fluidez, el sentido pedagógico y la conexión emocional con el entorno étnico, de modo que la experiencia sea educativa, significativa y memorable.

          .row(numero="Evaluación:&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp" titulo="")
            .col-md-3
              figure
                img(src='@/assets/curso/temas/tema1/tema1/tema11-rec3-img3.svg', alt='Texto que describa la imagen')
                
            .col-md-9.mb-4.mb-md-0
              p.color-adicional-4.lh-1 En esta fase, una vez finalizado el recorrido, se realiza un análisis de retroalimentación que permite valorar el nivel de satisfacción de las personas visitantes, mediante herramientas de evaluación como encuestas o listas de verificación (checklist) con preguntas orientadas a medir la satisfacción del cliente. 
              p.color-adicional-4.lh-1 Además, se examina la pertinencia de la planificación y se identifican oportunidades de mejora que garanticen la calidad y sostenibilidad del producto turístico.
       

    br
    .row.justify-content-center.align-items-center.mb-4(data-aos="flip-up")
      p Para llevar a cabo un desarrollo del recorrido eficaz en las fases mencionadas, es necesario tener en cuenta los siguientes componentes que hacen parte de la estructura:

    br
    .row.justify-content-center(data-aos="fade-left")
      .col-lg-10
        figure
          img(src='@/assets/curso/temas/tema1/tema1/tema1-tabla2.svg', alt='Texto que describa la imagen')

    br
    .row.justify-content-center.align-items-center.mb-4(data-aos="flip-up")
      p En contextos étnicos, se hace necesario preservar con mayor exigencia algunos principios fundamentales del recorrido turístico, para mantener la coherencia argumental y el respeto cultural de su estructura. A continuación, se presentan los principios básicos del recorrido:
      .col-md-11
        .p •	Seguridad: prioridad absoluta durante todo el trayecto.
        .p •	Sostenibilidad: respeto por el entorno natural y cultural.
        .p •	Autenticidad: valoración del patrimonio y de las comunidades locales.
        .p •	Participación: integración activa de las personas visitantes.
        .p •	Educación: transmisión de valores y conocimientos orientados a la responsabilidad ambiental y social.
    
    br
    .row.justify-content-center.align-items-center.mb-4(data-aos="flip-up")
      p Bajo estos principios, resulta esencial desarrollar los elementos operativos que garanticen un manejo eficaz, seguro y satisfactorio del recorrido turístico:
    
    .row.justify-content-center(data-aos="fade-right")
      .col-md-11.mb-5.mb-md-0
        LineaTiempoD
          .row(numero="Elementos logísticos:&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;" titulo="")
            .col-md-3
              figure
                img(src='@/assets/curso/temas/tema1/tema1/tema11-rec4-img1.svg', alt='Texto que describa la imagen')
                
            .col-md-9.mb-4.mb-md-0
              p.color-adicional-4.lh-1 Permiten la organización y el funcionamiento operativo del recorrido turístico. Garantizan que las actividades se desarrollen de manera ordenada, eficiente y confortable para las personas visitantes.
              p.color-adicional-4.lh-1 Ejemplos: transporte local, horarios planificados, señalización ecológica, zonas de descanso, control de visitantes y coordinación con la comunidad.

          .row(numero="Elementos interpretativos:" titulo="")
            .col-md-3
              figure
                img(src='@/assets/curso/temas/tema1/tema1/tema11-rec4-img2.svg', alt='Texto que describa la imagen')
                
            .col-md-9.mb-4.mb-md-0
              p.color-adicional-4.lh-1 Constituyen los medios y estrategias comunicativas que facilitan la transmisión del valor del territorio, los atractivos y la cultura local, de manera educativa y significativa.
              p.color-adicional-4.lh-1 Ejemplos: guion interpretativo construido con líderes locales, demostraciones artesanales, narración de mitos, rutas simbólicas y cartelería bilingüe (lengua indígena y español)

          .row(numero="Elementos de seguridad:&nbsp;&nbsp;&nbsp" titulo="")
            .col-md-3
              figure
                img(src='@/assets/curso/temas/tema1/tema1/tema11-rec4-img3.svg', alt='Texto que describa la imagen')
                
            .col-md-9.mb-4.mb-md-0
              p.color-adicional-4.lh-1 Incluyen los procedimientos y recursos preventivos destinados a reducir riesgos y proteger la integridad física y emocional de las personas visitantes durante el recorrido.
              p.color-adicional-4.lh-1 Ejemplos: evaluación de riesgos, botiquines de primeros auxilios, puntos seguros, capacitación en atención de emergencias y acompañamiento de guías locales conocedores del territorio.
   

    br
    .row.justify-content-center.align-items-center.mb-4(data-aos="flip-up")
      p En el marco del turismo étnico sostenible, los elementos logísticos, interpretativos y de seguridad deben diseñarse desde una perspectiva participativa y comunitaria, donde la organización, la comunicación cultural y la protección del visitante sean coherentes con los valores de respeto, identidad y sostenibilidad.
      p De esta forma, el recorrido turístico se convierte no solo en una experiencia recreativa, sino también en un espacio de diálogo intercultural, aprendizaje colectivo y conservación del patrimonio vivo.


</template>

